function productCardDropdown() {
  let buttons = $('.product-card-dropdown__title');
  let texts = $('.product-card-dropdown__description');
  let arrows = $('.product-card-dropdown__arrow');

  buttons.each(function(index) {
    $(this).on('click', function() {
      texts.eq(index).toggleClass('active');
      arrows.eq(index).toggleClass('active');
    });
  });
}
productCardDropdown();