import Swiper from 'swiper';
import { Thumbs, EffectFade } from 'swiper/modules';

function brandsSlider() {

  var swiperThumbs = new Swiper(".brands-card-thumbs1", {
    spaceBetween: 10,
    slidesPerView: 3,
  });

  // 1
  new Swiper(".brands-card-slider1", {
    modules: [EffectFade, Thumbs],
    slidesPerView: 1,
    grabCursor: true,
    effect: 'fade',
    fadeEffect: {
      crossFade: true
    },
    thumbs: {
      swiper: swiperThumbs,
    },
  });
  
  var swiperThumbs2 = new Swiper(".brands-card-thumbs2", {
    spaceBetween: 10,
    slidesPerView: 3,
  });

  // 1
  new Swiper(".brands-card-slider2", {
    modules: [EffectFade, Thumbs],
    slidesPerView: 1,
    grabCursor: true,
    effect: 'fade',
    fadeEffect: {
      crossFade: true
    },
    thumbs: {
      swiper: swiperThumbs2,
    },
  });

  var swiperThumbs3 = new Swiper(".brands-card-thumbs3", {
    spaceBetween: 10,
    slidesPerView: 3,
  });

  // 1
  new Swiper(".brands-card-slider3", {
    modules: [EffectFade, Thumbs],
    slidesPerView: 1,
    grabCursor: true,
    effect: 'fade',
    fadeEffect: {
      crossFade: true
    },
    thumbs: {
      swiper: swiperThumbs3,
    },
  });

};
brandsSlider();
